@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
.wrapper_main {
    text-align: center;
    padding: 30px;
}

.location {
    font-size: 38px;
    margin-bottom: 10px;
}

.description {
    font-size: 24px;
    margin-bottom: 20px;
}

.temperature {
    font-size: 84px;
}

.mini-credit {
    font-size: 10px;
    margin-left: 10px;
}
.wrapper_box {
    background-color: rgb(247, 247, 247);
    padding: 30px;
    display: grid;
}
.wrapper_header {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 20px;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

@media only screen and (max-width: 520px) {
    .wrapper_header {
        grid-template-columns: 1fr;
        place-items: center;
    }
}
.wrapper_date {
    display: flex;
    align-items: center;
}
.search {
    height: 40px;
    font-size: 18px;
    font-family: "Varela Round", sans-serif;
    color: #303030;
    text-align: right;
    padding: 0 10px;
    border: none;
    border-radius: 10px;
}

@media only screen and (max-width: 520px) {
    .search {
        width: 100%;
        text-align: center;
    }
}
.wrapper_Mcard {
    background: rgba(255, 255, 255, 0.95);
    padding: 20px;
    text-align: right;
    border-radius: 20px;
}

.content {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

@media only screen and (max-width: 475px) {
    .content {
        grid-template-columns: 1fr 2fr;
    }
}
.wrapper_metric {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

@media only screen and (max-width: 600px) {
    .wrapper_metric {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 475px) {
    .wrapper_metric {
        grid-template-columns: 1fr;
    }
}
.wrapper_switch {
    text-align: right;
}

.switch {
    display: inline;
    margin: 0 10px;
    cursor: pointer;
}

.active {
    color: green;
}

.inactive {
    color: black;
}

@media only screen and (max-width: 475px) {
    .wrapper {
        text-align: center;
    }
}

@media only screen and (max-width: 335px) {
    .wrapper {
        display: grid;
        grid-template-columns: 1fr;
    }
    .switch {
        margin: 10px 0;
    }
}
.wrappe_error {
    max-width: 260px;
    text-align: center;
}

.message {
    margin-bottom: 30px;
}
.wrapper_home {
    display: grid;
    grid-template-columns: 1fr 2fr;
    max-width: 1300px;
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 8px 32px 0 rgba(83, 89, 179, 0.37);
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(3px);
    border-radius: 30px;
    overflow: hidden;
}

@media only screen and (max-width: 950px) {
    .wrapper_home {
        grid-template-columns: 1fr;
        max-width: 600px;
        margin: 20px auto;
    }
}

@media only screen and (max-width: 600px) {
    .wrapper_home {
        margin: 0;
        border-radius: 0;
    }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  background-image: radial-gradient(
    circle 993px at 0.5% 50.5%,
    rgba(137, 171, 245, 0.37) 0%,
    rgba(245, 247, 252, 1) 100.2%
  );
  font-family: "Varela Round", sans-serif;
}

